@import url(https://fonts.googleapis.com/css2?family=Alata&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap);
*{
margin: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a{
  text-decoration: none;
  color: white;
}
.header{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color:white;
    background-color:black;
    top:0;
    position: -webkit-sticky;
    position: sticky;
    z-index: 2000;
}

.header_left{
    -webkit-flex: 0.4 1;
            flex: 0.4 1;

}

.header_left>h1{
    font-family: "Alata",sans-serif;
    letter-spacing: 1px;
    padding: 20px 0;
}

.header_right{
    display: -webkit-flex;
    display: flex;
    -webkit-flex:0.6 1;
            flex:0.6 1;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;

}

.header_right>a>h4{
font-size: 18px;
cursor: pointer;
transition: -webkit-transform 200ms ease-in-out;
transition: transform 200ms ease-in-out;
transition: transform 200ms ease-in-out, -webkit-transform 200ms ease-in-out;
padding: 20px 0;
letter-spacing: 1px;
}

.header_right>a>h4:hover{
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
    background-color: blueviolet;
    color: white;
    padding: 5px 20px;
    border-radius: 2px;
    letter-spacing: 1px;
    font-family: "Crimson Text",serif;
    font-size: 15px;
    cursor: pointer;

}
.topContent{
    color: white;
    position: relative;
}
.topContent_container{
    position: absolute;
     top:150px;
     right: 250px;
}

.topContent_container>img{
        width: 100px;
        height: 100px;
        border-radius: 75px;
        position: relative;
        margin-left: 100px;
        
        
}

.topContent_container>p{
  letter-spacing: 1px;
  text-align: left;
  padding-left: 3px;
  padding-top: 20px;  
}

.topContent_container>a>button{
    margin-top: 30px;
    text-align: left;
    margin-left: 5px;
    border: none;
    border-radius: 2px;
    outline: none;
}

.topContent_container>h1{
    font-size: 40px;
    font-family: "Montserrat",sans-serif;
}

.topContent_download{
    color:white;
    background-color:black;
    font-size: 14px;
    padding: 10px 10px;
    font-family: "Montserrat",sans-serif;
    cursor: pointer;
    margin-right: 20px;

}
.topContent_download:hover{
    color:black;
    background-color:white;
    font-weight: 600;

}

.topContent_work{
    color:white;
    background-color:transparent;
    border: 1px solid white !important;
    font-size: 14px;
    padding: 9px 45px;
    font-family: "Montserrat",sans-serif;
    cursor: pointer;
    font-weight: 300;
}
.topContent_work:hover{
    color: black;
    background-color: white;
    font-weight: 600;
}

.topContainer{
    background-color: #9174bb;
    height: 100vh;
    background-position: center;
    background-size: cover;
    background-repeat:no-repeat;
}
.skillcontainer{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 20px 0;
}
.skillcontainer_text{
    -webkit-flex:1 1;
            flex:1 1;
    text-align: left;
    max-width: 500px;
    margin-bottom: 20px;
}
.skillcontainer_text>h2{
    padding-left: 100px;
    font-size: 30px;
}


.skillcontainer_skillset>h5{
    font-family: "Montserrat", sans-serif;
    font-weight: 300;
    padding-bottom: 30px;
    padding-top: 10px;

}

.skillcontainer_slider>.MuilinearProgress-colorPrimary{
background-color: rgba(88,88,88)
}

.skillcontainer_slider1>div>.MuilinearProgress-colorPrimary{
    background-color:rgba(green);
}
.skillcontainer_slider2>div>.MuilinearProgress-colorPrimary{
    background-color:green;
}
.skillcontainer_slider3>div>.MuilinearProgress-colorPrimary{
    background-color: red;
}
.project> img{
    width:350px;
    height:179px;
}
.project{
    margin:15px;
    width: 350px;
    height: 179px;
    transition: -webkit-transform 100ms ease in;
    transition: transform 100ms ease in;
    transition: transform 100ms ease in, -webkit-transform 100ms ease in;
}

.project:hover{
    color:white;
    background-color: blueviolet;
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
}

.project_content{
    padding: 10px;
}

.project_content>h4{
    padding-bottom: 5px;
}

.project_content>p{
    font-size: 15px;
}
.projectcontainer{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    color:black;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 200px 0;
    -webkit-align-items: center;
            align-items: center;
}
.projectcontainer>h1{
    padding-bottom: 15px;
}

.projectcontainer>p{
    padding-bottom: 20px;
}

.projectContainer_projects{
    display: -webkit-flex;
    display: flex;
    width:60vw;
    margin-top: 50px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.contact{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
-webkit-justify-content: center;
        justify-content: center;
margin: 200px 0 100px;
-webkit-align-items: center;
        align-items: center;
}

.contact_container{
    border:1px solid black;
    width:600px;
    height: 200px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 30px;
    margin-top: 30px;
}
.contact_container>p{
    font-size: 20px;
    text-align: left;
    padding-bottom: 30px;
}

.contact_container>p>span{
    color:blueviolet;
}

.contact_icons>a>button>span>.MuisvgIcon-root{
color: black;
font-size: 30px;
}

.contact_icons>a>.MuiIconButton-root{
    color:blueviolet !important;
}
